*, ::before, ::after {
    box-sizing: border-box;
}

hr {
    width: 80%;
}

#MessageButton:hover {
    transform: translateY(-3px);
}

#Message::-webkit-scrollbar {
    width: 6px;
}
#Message::-webkit-scrollbar-thumb {
    background-color: #ededed;
    border: 1px solid #fff;
    border-radius: 10px;
}
#message::-webkit-scrollbar-track {
    background-color: #fff;
}

#easteregg {
    right: 0;
    cursor: default;
}